@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;400;500;600&display=swap');
* {
    font-family: 'Poppins', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    outline: none;
    border: none;
    text-decoration: none;
    text-transform: capitalize;
    transition: all 0.2s linear;
}

$blue: #29d9d5;
$black: #111;
$white: #fff;
$light-color: #aaa;
$bg-color: #222;
$black-bg: rgba(17,
17,
17,
0.7);
$border: 0.2rem solid $blue;
html {
    font-size: 62.5%;
    overflow-x: hidden;
    scroll-behavior: smooth;
    scroll-padding-top: 9rem;
    &::-webkit-scrollbar {
        width: 1rem;
    }
    &::-webkit-scrollbar-track {
        background: $black;
    }
    &::-webkit-scrollbar-thumb {
        background: $blue;
        border-radius: 5rem;
    }
}

@mixin grid($val) {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax($val, 1fr));
    gap: 1.5rem;
}

body {
    background: $black;
    overflow-x: hidden;
    font-size: 16px;
}

section {
    padding: 3rem 9%;
}

.sub-title {
    color: $blue;
    letter-spacing: 1px;
    font-size: 2vw;
}

.title {
    color: $white;
    letter-spacing: 1px;
    font-size: 3.2vw;
}

.text {
    margin: 20px 0;
    color: $light-color;
}

.header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    z-index: 1000;
    justify-content: space-between;
    align-items: center;
    padding: 15px 8%;
    background-color: $bg-color;
}

.navbar {
    a {
        font-size: 17px;
        margin: 0 16px;
        color: $light-color;
        &:hover {
            color: $blue;
        }
    }
}

.logo {
    font-size: 25px;
    color: $white;
    .fa-paper-plane {
        color: $blue;
    }
    &:hover {
        transform: scale(0.9);
    }
}

.btn {
    display: inline-block;
    padding: 10px 30px;
    border: 2px solid $blue;
    border-radius: 30px;
    font-size: 17px;
    color: $blue;
    &:hover {
        color: $bg-color;
        background-color: $blue;
    }
}

#menu-btn {
    display: none;
    font-size: 26px;
    color: $white;
    cursor: pointer;
    &:hover {
        transform: scale(1.1);
    }
}

.home {
    min-height: 80vh;
    width: 90%;
    margin: 0 auto;
    margin-top: 90px;
    background: linear-gradient(rgba(17, 17, 17, 0.7), rgba(17, 17, 17, 0.7)), url(../img/home-bg.jpg), no-repeat;
    background-size: cover;
    background-position: center;
    .content {
        min-height: 80vh;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color: $white;
    }
    .home__title {
        font-size: 4vw;
        font-weight: bolder;
        color: transparent;
        -webkit-text-stroke: 0.1rem $white;
    }
    .home__subtitle {
        font-size: 6vw;
    }
    .text {
        text-align: center;
    }
}

.book-form {
    width: 80%;
    padding: 30px 40px;
    background-color: $bg-color;
    margin: 0 auto;
    margin-top: -60px;
    border-radius: 30px;
    form {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        gap: 10px;
    }
    .input-box {
        flex: 1 1 25rem;
        span {
            display: block;
            color: $light-color;
            font-size: 13px;
            margin-bottom: 5px;
        }
        input {
            width: 100%;
            padding: 10px;
            background-color: transparent;
            border: solid 2px $blue;
            border-radius: 30px;
            font-size: 16px;
            color: $light-color;
        }
    }
    .btn {
        margin-top: 20px;
        background-color: transparent;
        &:hover {
            color: $white;
            background-color: $blue;
        }
    }
}

.about {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 25px;
    margin-top: 50px;
    .video-container {
        flex: 1 1 42rem;
    }
    .about__video {
        width: 100%;
        border-radius: 10px;
    }
    .about-content {
        flex: 1 1 42rem;
    }
    .about-controls {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 15px;
        .about__control-btn {
            display: inline-block;
            width: 18px;
            height: 18px;
            margin-top: 15px;
            border-radius: 50%;
            background-color: $white;
            cursor: pointer;
            &:hover {
                transform: scale(1.2);
            }
        }
        .active {
            background-color: $blue;
        }
    }
}

.destination {
    .destination-heading {
        text-align: center;
    }
    .box-container {
        @include grid(27rem);
        margin-top: 50px;
        .box {
            overflow: hidden;
            background-color: $bg-color;
            border-radius: 20px;
            cursor: pointer;
            &:hover img {
                transform: scale(1.1);
            }
        }
    }
    .box-img {
        width: 100%;
        height: 20rem;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    .box-content {
        text-align: center;
        padding: 15px 2px;
        .box__title {
            color: $white;
        }
        .text {
            font-size: 12px;
        }
        .box__link {
            display: inline-block;
            color: $blue;
            margin: 10px;
            cursor: pointer;
            &:hover {
                .fas {
                    margin-left: 10px;
                }
            }
        }
    }
}

.service {
    .box-container {
        @include grid(27rem);
        .box {
            text-align: center;
            padding: 25px 15px;
            background-color: $bg-color;
            border-radius: 10px;
            &:hover {
                transform: scale(1.05);
            }
        }
    }
    .fas {
        color: $blue;
        font-size: 4vw;
        margin-bottom: 10px;
    }
    .service__title {
        color: $white;
    }
    .text {
        font-size: 14px;
    }
}

.gallery {
    .heading {
        text-align: center;
    }
}

.gallery {
    .box-container {
        columns: 3 25rem;
        column-gap: 1.5rem;
    }
    .box {
        border: 1px solid $bg-color;
        text-align: center;
        border-radius: 10px;
        margin-bottom: 40px;
        break-inside: avoid;
        &:hover img {
            transform: scale(1.02);
        }
    }
    .gallery__img {
        width: 100%;
        border-radius: 1rem;
        margin-bottom: 1rem;
    }
    .gallery__subtitle {
        color: $light-color;
    }
    .gallery__title {
        color: $white;
        font-size: 24px;
    }
}

.review {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    .review-content {
        flex: 1 1 500px;
    }
    .text {
        font-size: 14px;
    }
    .box-conteiner {
        display: flex;
        flex-wrap: wrap;
        flex: 1 1 500px;
        gap: 20px;
    }
    .box {
        flex: 1 1 230px;
        background-color: $bg-color;
        padding: 10px 25px;
        border-radius: 10px;
        border: 2px solid transparent;
        cursor: pointer;
        &:hover {
            border: 2px solid $blue;
        }
        .review-inner {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            gap: 15px;
        }
        .avatar {
            width: 60px;
            height: 60px;
            border-radius: 50%;
        }
        .name {
            display: block;
            color: $white;
            font-weight: bold;
            font-size: 20px;
        }
        .profesion {
            color: $light-color;
        }
    }
}

.blog {
    .heading {
        text-align: center;
        margin-bottom: 50px;
    }
    .box-container {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        gap: 15px;
    }
    .box {
        flex: 1 1 300px;
        max-width: 360px;
        border-radius: 20px;
        background-color: $bg-color;
        &:hover img {
            transform: scale(1.1);
        }
    }
    .blog__img {
        width: 100%;
        height: 250px;
        border-radius: 20px;
        overflow: hidden;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    .blog-content {
        padding: 10px;
    }
    .content__title {
        color: $white;
        font-size: 1.5vw;
    }
    .blog-info {
        display: flex;
        justify-content: space-between;
        a {
            color: $white;
            &:hover {
                color: $blue;
                transform: scale(1.05);
            }
        }
        .fas {
            margin-right: 8px;
        }
    }
}

.banner {
    background: linear-gradient(rgba(17, 17, 17, 0.7), rgba(17, 17, 17, 0.7)), url(../img/home-bg.jpg), no-repeat;
    padding: 50px;
    background-attachment: fixed;
    background-size: cover;
    background-position: center;
    text-align: center;
    .sub-title {
        font-size: 1.6vw;
    }
    .title {
        font-size: 2.5vw;
    }
    .text {
        margin: 30px auto;
        max-width: 800px;
    }
}

.footer {
    margin-top: 50px;
    .box-container {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    .text {
        font-size: 14px;
        max-width: 260px;
    }
    .share {
        a {
            width: 45px;
            height: 45px;
            margin-right: 8px;
            border: 2px solid $blue;
            border-radius: 50%;
            color: $blue;
            font-size: 22px;
            line-height: 4rem;
            text-align: center;
            display: inline-block;
            &:hover {
                background-color: $blue;
                color: $bg-color;
            }
        }
    }
    .footer__title {
        color: $white;
        font-size: 1.6vw;
        margin-bottom: 20px;
    }
    .quick-links {
        a {
            display: block;
            color: $light-color;
            margin-bottom: 12px;
            font-size: 14px;
            &:hover {
                color: $blue;
                .fas {
                    margin-right: 15px;
                }
            }
        }
        .fas {
            color: $blue;
            margin-right: 5px;
        }
    }
    .contact-info {
        p {
            font-size: 14px;
            margin-bottom: 12px;
            color: $light-color;
        }
        i {
            margin-right: 5px;
            color: $blue;
        }
    }
    .news-letter {
        p {
            font-size: 14px;
            margin-bottom: 12px;
            color: $light-color;
        }
    }
    .form-footer {
        .email {
            display: block;
            background-color: transparent;
            padding: 10px 30px;
            border: 2px solid $blue;
            border-radius: 30px;
            font-size: 17px;
            color: $blue;
        }
        .btn {
            background-color: transparent;
            margin-top: 15px;
            &:hover {
                background-color: $blue;
            }
        }
    }
}

.creater {
    padding: 25px;
    background-color: $bg-color;
    color: $white;
    font-size: 2vw;
    font-weight: bold;
    text-align: center;
}

//media query
@media (max-width: 991px) {
    html {
        font-size: 50%;
    }
    .header {
        padding: 24px 28px;
    }
    .logo {
        font-size: 22px;
    }
    .navbar {
        a {
            font-size: 15px;
            margin: 0 8px;
        }
    }
    .btn {
        font-size: 15px;
    }
    .blog {
        .content__title {
            color: $white;
            font-size: 2.2vw;
        }
    }
    .footer {
        .footer__title {
            font-size: 2.5vw;
        }
    }
}

@media (max-width: 768px) {
    #menu-btn {
        display: inline-block;
    }
    .navbar {
        position: absolute;
        top: 99%;
        left: 0;
        right: 0;
        background: $bg-color;
        clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
        &.active {
            clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
        }
        a {
            margin: 20px 30px;
            font-size: 18px;
            display: block;
        }
    }
    .home {
        .home__title {
            font-size: 6vw;
        }
        .home__subtitle {
            font-size: 8vw;
        }
    }
    .sub-title {
        font-size: 2.6vw;
    }
    .title {
        font-size: 4.2vw;
    }
    .text {
        font-size: 14px;
    }
    .blog {
        .content__title {
            color: $white;
            font-size: 3.2vw;
        }
    }
    .creater {
        font-size: 3vw;
    }
}

@media (max-width: 450px) {
    html {
        font-size: 50%;
    }
    .sub-title {
        font-size: 5vw;
    }
    .title {
        font-size: 5.5vw;
    }
    .text {
        font-size: 12px;
    }
}